@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin onTablet() {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin onDesktop() {
  @media (min-width: 1280px) {
    @content;
  }
}

@mixin onDesign() {
  @media (min-width: 1440px) {
    @content;
  }
}

@mixin onLargeScreens() {
  @media (min-width: 2560px) {
    @content;
  }
}
